import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SettingsComponent } from './settings.component';
import { AccountComponent } from './account/account.component';
import { PersonalInformationComponent } from './account/personal-information/personal-information.component';
import { ChangeEmailComponent } from './account/personal-information/change-email/change-email.component';
import { ChangeInterestsComponent } from './account/personal-information/change-interests/change-interests.component';
import { ProfileSettingsComponent } from './account/personal-information/profile-settings/profile-settings.component';

import { SecurityComponent } from './security/security.component';
import { SavedLoginInfoComponent } from './security/saved-login-info/saved-login-info.component';

// import { PrivacyComponent } from './privacy/privacy.component';
// import { AdsComponent } from './ads/ads.component';
// import { NotificationsComponent } from './notifications/notifications.component';
import { SupportComponent } from './support/support.component';
import { AboutComponent } from './about/about.component';
import { PrivacyPolicyComponent } from './about/privacy-policy/privacy-policy.component';
import { TermsOfUseComponent } from './about/terms-of-use/terms-of-use.component';
import { AboutIn2artComponent } from './about/about-in2art/about-in2art.component';

const routes: Routes = [
    {
        path: 'account',
        children: [
            {
                path: 'personal-information',
                children: [
                    {
                        path: 'change-email',
                        component: ChangeEmailComponent
                    },
                    {
                        path: 'change-interests',
                        redirectTo : '/interests/medium'
                    },
                    {
                        path: 'profile-settings',
                        component: ProfileSettingsComponent
                    },
                    {
                        path: '',
                        component: PersonalInformationComponent
                    },
                ]
            },
            {
                path: '',
                component: AccountComponent,
            }
        ]
    },
    {
        path: 'security',
        children: [
            {
                path: 'reset-password',
                redirectTo: "/auth/resetpassword"
            },
            {
                path: 'saved-login-info',
                component: SavedLoginInfoComponent
            },
            {
                path: '',
                component: SecurityComponent
            }
        ]
    },
    // {
    //     path: 'privacy',
    //     component: PrivacyComponent
    // },
    // {
    //     path: 'ads',
    //     component: AdsComponent
    // },
    // {
    //     path: 'notifications',
    //     component: NotificationsComponent
    // },
    {
        path: 'support',
        component: SupportComponent
    },
    {
        path: 'about',
        children: [
            {
                path: 'privacy-policy',
                component: PrivacyPolicyComponent
            },
            {
                path: 'terms-of-use',
                component: TermsOfUseComponent
            },
            {
                path: 'about-in2art',
                component: AboutIn2artComponent
            },
            {
                path: '',
                component: AboutComponent
            }
        ]
    },
    {
        path: '',
        component: SettingsComponent
}];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class SettingsRoutingModule { }
