import { Utils } from '../../../../../services/utils.service';
import { MainService } from '../../../../../services/main.service';
import * as $ from "jquery";

import { Component, OnInit, ViewChild } from '@angular/core';

@Component({
    selector: 'change-email',
    templateUrl: './change-email.component.html',
    styleUrls: ['./change-email.component.scss']
})
export class ChangeEmailComponent implements OnInit {

    constructor(public main: MainService) {
        main.newPage(this);
    }

    async ngOnInit() {
        let saveLogin = false;
        
        $("#saveLogin").prop("checked", saveLogin);
    }
    ngOnDestroy() { this.main.onDestroyPage(); }

    guardarCanvis() {
        let saveLogin = $("#saveLogin").prop("checked");

        if (saveLogin)
            Utils.sessionToCookie("currentUser");
        else
            Utils.cookieToSession("currentUser");
        
        this.main.toastSuccess("Changes saved");
        // console.log("Cookie", localStorage.getItem("currentUser"));
        // console.log("Session", sessionStorage.getItem("currentUser"));
    }
}
