import { Component, OnInit, ViewChild } from '@angular/core';

import { SettingsComponent } from './../settings.component';
import { AboutComponent } from './../about/about.component';
import { AccountComponent } from './../account/account.component';
import { SecurityComponent } from './../security/security.component';
import { SupportComponent } from './../support/support.component';

@Component({
    selector: 'app-buscador',
    templateUrl: './buscador.component.html',
    styleUrls: ['./buscador.component.scss']
})
export class BuscadorComponent implements OnInit {

    @ViewChild("input") input;
    
    public optionsFull = [];
    public options = null;

    constructor() {
        // Recopilem totes les llistes d'opcions de totes les categories //
        this.optionsFull = this.optionsFull
            .concat(SettingsComponent.options)
            .concat(AboutComponent.options)
            .concat(AccountComponent.options)
            .concat(SecurityComponent.options)
            .concat(SupportComponent.options)
    }

    async ngOnInit() { }

    filtrar(text) {
        if (text == "") this.options = null;
        else {
            this.options = this.optionsFull.filter((e, i) => {
                return e.name.toLowerCase().includes(text.toLowerCase());
            });
        }
    }
}
