import { Utils } from './../../../../services/utils.service';
import { MainService } from './../../../../services/main.service';
import * as $ from "jquery";

import { Component, OnInit, ViewChild } from '@angular/core';

@Component({
    selector: 'app-saved-login-info',
    templateUrl: './saved-login-info.component.html',
    styleUrls: ['./saved-login-info.component.scss']
})
export class SavedLoginInfoComponent implements OnInit {

    public saveLogin;
    public readonly key = "currentUser";

    constructor(public main: MainService) {
        main.newPage(this);
    }

    async ngOnInit() {
        this.saveLogin = Utils.cookieStorageGet(this.key);
    }
    ngOnDestroy() { this.main.onDestroyPage(); }

    guardarCanvis() {
        if (this.saveLogin) {
            let value = Utils.sessionStorageGet(this.key);
            if (!value) return;
            Utils.cookieStorageSet(this.key, value);
        } else {
            let value = Utils.cookieStorageGet(this.key);
            if (!value) return;
            Utils.sessionStorageSet(this.key, value);
            Utils.cookieStorageRemove(this.key);
        }
        
        this.main.toastSuccess("Changes saved");
    }

}
